@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
body{margin:0;padding:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{box-sizing:border-box;margin:0;padding:0}body{background:#f5f5f5;font-family:"Poppins", sans-serif}h1{margin:20px 0;color:#b383e2;font-size:7rem;text-align:center;opacity:.4;font-weight:600}form{margin:auto;max-width:100%;width:500px;box-shadow:0 5px 10px rgba(0,0,0,0.1)}.input{padding:20px 30px;font-size:2rem;width:500px;height:60px;border:none;outline:none;border-radius:1px;color:#777}.input::-webkit-input-placeholder{color:#b5b5b5}.input:-ms-input-placeholder{color:#b5b5b5}.input::placeholder{color:#b5b5b5}.ul{cursor:pointer;max-width:500px;width:100%;font-size:1.5rem;margin:0 auto;list-style:none;box-shadow:0 5px 10px rgba(0,0,0,0.1)}.list{border-top:1px solid #f1f0f0;background:#fff;padding:10px 30px;color:#777;overflow:hidden;width:100%}.clear-btn{position:absolute;top:13%;left:70%;cursor:pointer;border:none;font-size:30px;color:white;background:#e8d5fb;border-radius:30px;padding:7px 7px 0 7px;text-align:center}.completed{text-decoration:line-through;color:#d5d5d5 !important}.instruction{margin:30px 0 20px;color:#b5b5b5}small{display:flex;align-items:center;justify-content:center;margin-top:5px}@media (max-width: 800px){h1{font-size:5.5rem}.ul{max-width:400px}form{max-width:400px}.input{max-width:400px}}@media (max-width: 600px){h1{font-size:4rem}.ul{max-width:300px}form{max-width:300px}.input{max-width:300px}}

